import React from 'react'
import './Menu.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGlassCheers} from '@fortawesome/free-solid-svg-icons'
import {Footer} from './Footer'
export const Menu = (props) => {
    return(
        <section className="l-full-size">
            <div className ="m-center menu-main-background"  title = "bebida">
                <div className ="m-center-text m-main-text">
                    <header>
                       <h1>Menu</h1>
                    </header>
                    <p>Aproveite a variedade de bebidas !</p>
                    <FontAwesomeIcon icon={faGlassCheers}></FontAwesomeIcon>
                </div>
            </div>
            <div className="m-center menu-list-container color-black">
                <div className ="menu-list">
                     <div className ="list-category">
                         <header>Whisky</header>
                        <ul>
                            <li>Whisky 1....................................................................... R$10,00</li>
                            <li>Whisky 2....................................................................... R$20,00</li>
                            <li>Whisky 3....................................................................... R$30,00</li>
                        </ul>
                    </div>

                    <div className ="list-category">
                         <header>Vodka</header>
                        <ul>
                            <li>Vodka 1....................................................................... R$10,00</li>
                            <li>Vodka 2....................................................................... R$20,00</li>
                            <li>Vodka 3....................................................................... R$30,00</li>
                        </ul>
                    </div>

                    <div className ="list-category">
                         <header>Caipirinha</header>
                        <ul>
                            <li>Caipirinha 1....................................................................... R$10,00</li>
                            <li>Caipirinha 2....................................................................... R$20,00</li>
                            <li>Caipirinha 3....................................................................... R$30,00</li>
                        </ul>
                    </div>
                </div>
            </div>
            <Footer></Footer>
        </section>
    )
}